<template>
  <div class="payplugUISwitch" :class="(switchDisabled) ? '-disabled': ''">
    <label class="_switch" :data-e2e-name="switchData">
      <input type="checkbox"
		@change="updateStatus"
		:disabled="(switchDisabled) ? 'disabled' : null"
		:checked="this.checked"
		:name="switchName">
      <span class="_slider"></span>
      <span v-if="switchEnabledLabel" class="_label"
            :data-enable-text="(switchEnabledLabel) ? switchEnabledLabel : null"
            :data-disable-text="(switchDisabledLabel) ? switchDisabledLabel : null"></span>
    </label>
  </div>
</template>

<script>
import { store } from "@/store/store";
import {$enableAmericanExpress, $enableApplePay, $enableBancontact, $enableStandard} from "@/store/ajax";

export default {
	name: "AtomSwitch",
	props: {
		switchName: {
			type: String,
			required: false
		},
		switchDisabled: {
			type: String,
			required: false
		},
		switchChecked: {
			type: String,
			required: false
		},
		switchEnabledLabel: {
			type: String,
			required: false
		},
		switchDisabledLabel: {
			type: String,
			required: false
		},
		switchData: {
			type: String,
			required: false
		},
  },
	data() {
		return {
			store: store,
			checked: false
		}
	},
	created() {
		this.store.storeValue("enable_" + this.switchName, this.checked || false)
	},
	updated() {
		this.store.storeValue("enable_" + this.switchName, this.checked || false)

	},
	methods: {
		async updateStatus(e) {
      this.store.storeValue("enable_" + this.switchName, e.target.checked)
			var response = null;
			switch (this.switchName) {
				case "standard":
					response = await $enableStandard(e.target.checked);
					if(!response.success){
						this.uncheck(e);
						store.error.payment_methods = response.data;
					}

					break;
				case "applepay":
					response = await $enableApplePay(e.target.checked);
					if(!response.success){
						this.uncheck(e);
						store.error.payment_methods = response.data;
					}

					break;
				case "bancontact":
					response = await $enableBancontact(e.target.checked);
					if(!response.success){
						this.uncheck(e);
						store.error.payment_methods = response.data;
					}

					break;
				case "american_express":
					response = await $enableAmericanExpress(e.target.checked);
					if(!response.success){
						this.uncheck(e);
						store.error.payment_methods = response.data;
					}

					break;
				default:
					return null;
					// eslint-disable-next-line no-unreachable
					break;
			}
		},
		uncheck(e){
			//animation uncheck
			setTimeout(function(){
				e.target.checked = false
			}, 500)
		}
	}
}
</script>

