<template>
  <section class="payplugUiBlock stateBlock" data-e2e-name="blockState">
    <BlockHeader :blockHeaderTitle="title" :blockHeaderDescription="description" />
    <Accordion class="-stateBlock">
      <TextAlert :textAlertText="errorMessage" textAlertType="-warning"/>
      <div class="_stateContent">
        <PayplugButton :buttonText="check" class="-tertiary" />
        <div class="indicators">
          <Indicator v-for="(requirement, i) in requirements.options" :status="requirement.status" :text="requirement.text" v-bind:key="'indicator_' + i"/>
        </div>
      </div>
      <payplugUICheckbox :checked="enable_debug_checked" :name="enable_debug_name" :label="enable_debug_label" :description="enable_debug_description" />
    </Accordion>
  </section>
</template>

<script>
import BlockHeader from "../molecules/MoleculeBlockHeader";
import Accordion from "../molecules/MoleculeAccordion";
import TextAlert from "../atoms/AtomTextAlert";
import PayplugButton from "../atoms/AtomButton";
import Indicator from "../molecules/MoleculeIndicator";
import payplugUICheckbox from "../molecules/MoleculeCheckbox";

export default {
  name: "OrganismSectionStatus",
  components:{
    Indicator, BlockHeader, Accordion, TextAlert, PayplugButton, payplugUICheckbox
  },
  props: ["environment"],
  data(){
    return {
      "title":this.jsonData.status.title,
      "description":null,
      "errorMessage":null,
      "check":null,
      "requirements": null,
      "enable_debug_label": null,
      "enable_debug_description": null,
      "enable_debug_name": null,
      "enable_debug_checked": null,
    }
  },
  created() {
    this.init()
  },
  updated() {
    this.init()
  },
  methods: {
    init(){
      this.description = this.jsonData.status.descriptions[this.environment].description;
      this.errorMessage = this.jsonData.status.descriptions[this.environment].errorMessage;
      this.check = this.jsonData.status.descriptions[this.environment].check;
      this.enable_debug_label = this.jsonData.status.descriptions[this.environment].enable_debug_label;
      this.enable_debug_description = this.jsonData.status.descriptions[this.environment].enable_debug_description;
      this.requirements = this.jsonData.status.options[0];
      this.enable_debug_name = this.jsonData.status.enable_debug_name;
      this.enable_debug_checked = this.jsonData.status.enable_debug_checked;
    },
    showRegistration(){
      this.$emit('notRegisted', true)
    },
    showLoggedIn(){
      this.$emit('showLoggedIn', true)
    }
  }

}
</script>

