<template>
  <div class="payplugUIModal" :id="identifier">
    <label class="_overlay" @click="closeModal"></label>
    <div class="_modal" :class="(modalTitle) ? '' : ' -noTitle '">
      <label for="modalTriggered" class="_close" @click="closeModal">
        <Icon iconName="close"/>
      </label>
      <div v-if="modalTitle" class="_header">{{modalTitle}}</div>
      <div v-if="modalContent" class="_body" v-html="modalContent"></div>
      <div v-else class="_body">
        <slot/>
      </div>
    </div>
  </div>
</template>

<script>
import Icon from "../atoms/AtomIcon";
import { v4 as uuidv4 } from 'uuid';

export default {
  name: "MoleculeModal",
  components: {Icon},
  props: {
    modalTitle: {
      type: String,
      required: false
    },
    modalContent: {
      type: String,
      required: true,
      default: ""
    },
  },
  data: () => {
    return {
      identifier: uuidv4(),
    }
  },
  methods: {
    showModal() {
      document.getElementById(this.$data.identifier).classList.add("_show");
    },
    closeModal() {
      document.getElementById(this.$data.identifier).classList.remove("_show");
    }
  }
}
</script>

