<template>
  <div class="payplugUICheckbox">
    <label class="checkbox-label" :for="identifier">
      <input type="checkbox" :name="name" :checked="checked" @change="emitChange" :id="identifier"/>
      <span>{{ label }}</span>
    </label>
    <Paragraph :paragraphText="description"/>
  </div>
</template>

<script>
import {v4 as uuidv4} from "uuid";
import Paragraph from "../atoms/AtomParagraph";
import { store } from "@/store/store";

export default {
  name: "MoleculeCheckbox",
  components: {
    Paragraph
  },
  data: () => {
    return {
      store: store,
      identifier: uuidv4(),
    }
  },
  props: {
    label: {
      type: String,
      required: true,
      default: ""
    },
    description: {
      type: String,
      required: true,
      default: ""
    },
    name: {
      type: String,
      required: true,
    },
    checked: {
      type: String,
      required: true,
    },
  },
  created() {
    this.store.storeValue(this.name, this.checked || false)
  },
  methods: {
    emitChange(e) {
      this.store.storeValue(this.name, e.target.checked)
    }
  }
}
</script>

