<script>

import {store} from "@/store/store";
import PayplugOneClick from "@/classes/PaymentMethods/PaymentOptions/PayplugOneClick";
import PayplugEmbeded from "@/classes/PaymentMethods/PaymentOptions/PayplugEmbeded";

export default class PayplygStandard{

	type = null;
	name = null;
	title = null;
	image = null;
	checked = null;
	description = null;
	advanced_options = null;
	payment_options = null;
	options = [];

	constructor(data)
	{
		let that = this;
		const mode = store.manageMode();
		this.type = data.type
		this.name = data.name
		this.title = data.title
		this.image = data.image
		this.checked = data.checked
		this.description = data.descriptions[mode].description;
		this.advanced_options = data.descriptions[mode].advanced_options;

		data.options.map(function(payment_options_data){
			const payment_options = handlePaymentOptions(payment_options_data);
			if(payment_options !== null){
				that.options.push(payment_options)
			}

		});

		function handlePaymentOptions(data) {
			switch (data.name){
				case 'embeded':
					return new PayplugEmbeded(data);
					// eslint-disable-next-line no-unreachable
					break;
				case 'one_click':
					return new PayplugOneClick(data);
					// eslint-disable-next-line no-unreachable
					break;
				default:
					return null;
					// eslint-disable-next-line no-unreachable
					break;
			}
		}
	}

}

</script>
