<template>
  <div class="paymentMethodOption">
    <PMethodHeader :description="descriptionComputed ?? this.description" :title="paymentOption.title" :links="links" />
    <div class="_action">
		<payplugUIIOptions
			v-if="paymentOption.name == 'embeded'"
			class="_sandboxRadioButton"
			:optionsName="'payplug_' + paymentOption.name"
			:optionsItems="paymentOption.options"
			optionsData="embeddedMode"
			@update_embeded_option="handleEmbededOptionTranslation"
		/>
		<Switch
			v-if="paymentOption.name == 'one_click'"
			:switchName="paymentOption.name"
			:switchChecked="paymentOption.checked ? true : null"
			:switchData="paymentMethod.name == 'bancontact' ? 'bancontactCountrySwitch' : 'oneClickSwitch'"
		/>
    </div>
  </div>
</template>

<script>
import Switch from "../../atoms/AtomSwitch";
import PMethodHeader from "@/components/organisms/PMethod/OrganismPMethodHeader";
import payplugUIIOptions from "@/components/atoms/AtomOptions";
import {store} from "@/store/store";

export default {
	name: "OrganismPMethodOption",
	components:{payplugUIIOptions, PMethodHeader, Switch },
	computed:{
		environment:{
			get: () => store.settings.environment,
		},
		descriptionComputed:{
			get() {
				return store.data.payplug_embeded ? this.paymentOption["description_"+store.data.payplug_embeded] : this.description;
			}
		}
	},
	props: {
		paymentMethod:{
			type: Object,
			required: true
		},
		paymentOption:{
			type: Object,
			required: true
		}
	},
	created() {
		this.init()
	},
	updated() {
		this.init()
	},
	methods: {
		init(){
			this.description = this.paymentOption.description;
			this.links = this.paymentOption.link_know_more ?? null;
		},
		handleEmbededOptionTranslation(value){
			this.description = this.paymentOption["description_"+value];
		}
	}
}
</script>

